import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-search-field',
  templateUrl: './search-field.component.html',
  styleUrls: ['./search-field.component.css']
})
export class SearchFieldComponent implements OnInit {

  @Input('time') time: number = 1000;
  @Output('searchChange') eChange = new EventEmitter<string>();

  private timer;
 
  search: string = '';
  constructor() { }

  ngOnInit() {

  }

  onKeyPress(target) {
    if(this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }

    this.timer = setTimeout(() => this.eChange.emit(this.search), this.time);
  }

  onKeyDown(target) {
    if(target.key == 'Backspace') {
      this.onKeyPress(target);
    } 
  }

  onClean() {
    let oldSearch = this.search;
    this.search = '';

    if(oldSearch !== this.search)
      this.eChange.emit(this.search);
  }

}



