import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { OrderBy } from '../../../shared/pipes/orderby.pipe';

import { Good } from "../../good";
import { IGoodsListOptions } from '../goodsListOptions';
import { ToolsService } from '../../../shared/tools.service';

@Component({
  selector: 'app-goods-list',
  templateUrl: './goods-list.component.html',
  styleUrls: ['./goods-list.component.css']
})
export class GoodsListComponent implements OnInit {
  @Input('goods') goods: Good[] = [];
  @Input('options') set options(options: IGoodsListOptions) {
    this._options = {
      tools: {
        edit: !!options.tools.edit,
        delete: !!options.tools.delete,
        check: !!options.tools.check,
        add: !!options.tools.add
      },
      price: !!options.price,
      tags: !!options.tags
    }
  };

  @Output('check') eCheck: EventEmitter<Good> = new EventEmitter<Good>();
  @Output('delete') eDelete: EventEmitter<Good> = new EventEmitter<Good>();
  @Output('edit') eEdit: EventEmitter<Good> = new EventEmitter<Good>();
  @Output('add') eAdd: EventEmitter<Good> = new EventEmitter<Good>();

  currentItem: Good;
  selectedItems: Good[] = [];
  _options: IGoodsListOptions = {
    tools: {
      edit: true,
      delete: true,
      add: false,
      check: false
    },
    price: true,
    tags: true
  };

  get options() { return this._options; }

  constructor() { }

  ngOnInit() {
   
  }

  addItem(item) {
    this.selectItem(item);
  }

  deleteItem(item) {
    this.currentItem = item;
    this.eDelete.emit(item);
  }

  editItem(item) {
    this.currentItem = item;
    this.eEdit.emit(item);
  }

  clickItem(item) {
    if(this.isSelect(item)) this.unSelectItem(item);
    else this.selectItem(item);
    
    this.eCheck.emit(item);
  }

  selectItem(item) {
    this.selectedItems.push(item);
  }

  unSelectItem(item) {
    this.selectedItems.find((found, index) => {
      if(found._id == item._id) {
        this.selectedItems.splice(index, 1);
        return true;
      }
    });
  }

  isSelect(item: Good) {
    return !!this.selectedItems.find((found) => {
      if(found._id === item._id) return true;
    });
  }

  getSelectedItems() : Good[] {
    return this.selectedItems;
  }

  clearSelected() {
    this.selectedItems = []; 
  }

  onAdd(item: Good) {
    this.addItem(item);
    this.eAdd.emit(item);
  }

}



