import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
//import { MaterialModule } from '@angular/material';
import { MaterializeModule } from "angular2-materialize/src";
import { GoodsWidgetModule } from './_widgets/goods.widget';
import { SharedModule } from '../shared/shared.module';

import { GoodsComponent } from './goods/goods.component';
import { GoodAddComponent } from './good-add/good-add.component';

import { ToolbarService } from '../shared/toolbar.service';
import { SpecialtiesService } from '../+specialties/specialties.service';

import { GoodsService } from './goods.service';
import { GoodUpdateComponent } from './good-update/good-update.component';

 
const GOODS_ROUTES: Routes = [
  { path: '', component: GoodsComponent },
  { path: 'add', component: GoodAddComponent },
  { path: 'update/:id', component: GoodUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(GOODS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    GoodsWidgetModule,
    SharedModule
    //MaterialModule.forRoot()
  ],
  declarations: [GoodsComponent, GoodAddComponent, GoodUpdateComponent],
  providers: [GoodsService, SpecialtiesService],
  exports: [GoodsComponent]
})
export class GoodsModule { }


