import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Good } from '../good';
import { GoodsService } from '../goods.service';

import { SpecialtiesService } from '../../+specialties/specialties.service';
import { ToolsService } from '../../shared/tools.service';

@Component({
  selector: 'app-good-update',
  templateUrl: './good-update.component.html',
  styleUrls: ['./good-update.component.css']
})
export class GoodUpdateComponent implements OnInit {
  public good: Good;
  public tags: any[];
  public form: FormGroup;

  constructor(
    private _route: ActivatedRoute,
    private _tools: ToolsService,
    private _goodsService: GoodsService,
    private _specialtiesService: SpecialtiesService) { }

  ngOnInit() {
    this._tools.getToolbar().change({
      title: "Обновить товар"
    });

    this._specialtiesService.get()
      .subscribe((response) => this.tags = response);
      

    this.form = new FormGroup({
      _id: new FormControl('', [<any>Validators.required]),
      title: new FormControl('', [<any>Validators.required, <any>Validators.minLength(5)]),
      description: new FormControl(),
      price: new FormControl(),
			tags: new FormControl(''),
			active: new FormControl('')
    });

    this._route.params.forEach((params: Params) => {
			let id = params['id']; 
			this._goodsService.getOne(id)
				.subscribe(good => {
					this.good = good;
					(<FormGroup>this.form)
            			.patchValue(this.good);
				});
		});
  }

  clearForm(options?: any[]) : void {
    if(this.good != undefined) {
      this.good.clear();
    } else {
      this.good = new Good();
    }
  }

  onSubmit(values, valid) {
    //console.log(values);
    if(valid) {
      this._goodsService.update(values)
        .subscribe((data) => this._tools.getToast().toast("Товар успешно обновлен!"));
    }
  }

}



