import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { Good } from "../good";
import { ToolsService } from '../../shared/tools.service';
import { GoodsService } from '../goods.service';

import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-goods',
  templateUrl: './goods.component.html',
  styleUrls: ['./goods.component.css']
})
export class GoodsComponent implements OnInit {
  public goods$: Observable<Good[]>;
  public currentGood: Good;

  constructor(
    private _tools: ToolsService,
    private _goods: GoodsService,
    private _router: Router
    ) { 
      this.reloadGoods();
    }

  ngOnInit() {
    
    this._tools.getToolbar().change({
      title: "Список товаров",
      name: "goods",
      actions: {
        add: () => this._router.navigate(['goods/add'])
      },
      actionsPosition: {
        extend: ['add']
      }
    });
  }

  reloadGoods() {
    this.goods$ = this._goods.get();
    this.goods$.subscribe();
  }

  onEdit(item: Good) {
    this._router.navigate(['goods/update/', item._id]);
  }

  deleteItem(item) {
    let self = this;

    let remove$ = this._goods.remove(item._id);
    
    remove$.subscribe((data: any) => {
        if(data.status == 'ok') {
          this.reloadGoods();
          this._tools.getToast().toast("Товар удален");
        }
      });
  }

}



