import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.css']
})
export class ModalComponent implements OnInit {
  @Output('close')
    closed = new EventEmitter<string>();
  @Output('success')
    successfully = new EventEmitter<string>();

  public modal = new EventEmitter<string|MaterializeAction>();
  private show: boolean = false;
  public params = [
    {
      dismissible: false
    }
  ];

  constructor() { }

  ngOnInit() {

  }

  public open() {
    this.modal.emit({action:"modal", params:["open"]});
  }

  public close() {
    this.modal.emit({action: "modal", params:["close"]});
    this.closed.emit();
  }

  public success() {
    this.successfully.emit();
    this.modal.emit({action: "modal", params:["close"]});
  }

  public toggle() {
    this.show = !this.show;
    
    if(this.show) {
      this.open();
    } else {
      this.close();
    }
  }

}



