import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MaterializeModule } from 'angular2-materialize/src';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { HelpPanelComponent } from './help-panel/help-panel.component';
import { HelpPanelService } from './help-panel.service';
import { ModalComponent } from './modal/modal.component';
import { SelectedFilesComponent } from './files/selected-files/selected-files.component';
import { PaginationComponent } from './pagination/pagination.component';
import { AutocompleteComponent } from './forms/autocomplete/autocomplete.component';
import { ActionButtonsComponent } from './tables/action-buttons/action-buttons.component';
import { SearchFieldComponent } from './forms/search-field/search-field.component';

import { ClickOutsideDirective } from './directives/click-outside.directive';

//import { PaginationService } from './pagination.service';

@NgModule({
  imports: [
    CommonModule,
    MaterializeModule,
    ReactiveFormsModule, FormsModule
  ],
  //providers: [PaginationService],
  declarations: [
    HelpPanelComponent, 
    ModalComponent, 
    SelectedFilesComponent, 
    PaginationComponent, 
    AutocompleteComponent,
    ActionButtonsComponent, 
    SearchFieldComponent,
    ClickOutsideDirective
  ],
  exports: [
    HelpPanelComponent, 
    ModalComponent, 
    SelectedFilesComponent,
    PaginationComponent, 
    AutocompleteComponent,
    ActionButtonsComponent,
    SearchFieldComponent
  ]
})
export class SharedModule { }



