export interface IGood {
	_id?: string;
	title: string;
	description: string;
	price?: number;
	tags: string[];
	active: boolean;
}

export class Good implements IGood {
    _id?: string;
	title: string;
	description: string;
	price: number;
	tags: string[];
	active: boolean = true;

	constructor(object?: IGood){
		if(object) {
			this.setParams(object);
		}
	}

	setParams(options: {
		_id?: string;
		title?: string;
		description?: string;
		price?: number;
		tags?: string[];
		active?: boolean;
	}) {
		this._id = options._id || null;
		this.title = options.title || null;
		this.description = options.description || null;
		this.price = options.price || 0;
		this.tags = options.tags || [];
		this.active = options.active || false;
	}

	clear() : void {
		this._id = null;
		this.title = null;
		this.description = null;
		this.price = 0;
		this.tags = [];
		this.active = false;
	}
}



