import { Component, OnInit } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Good } from '../good';
import { GoodsService } from '../goods.service';

import { SpecialtiesService } from '../../+specialties/specialties.service';
import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';

@Component({
  selector: 'app-good-add',
  templateUrl: './good-add.component.html',
  styleUrls: ['./good-add.component.css']
})
export class GoodAddComponent implements OnInit {
  public form: FormGroup;
  public good: Good;
  public submitted: boolean = false;
  public tags: any[];

  constructor(
    private _toolbar: ToolbarService,
    private _goodsService: GoodsService,
    private _toastService: ToastService,
    private _specialtiesService: SpecialtiesService
    ) { }

  ngOnInit() {
    this._toolbar.change({
      title: "Добавить товар",
      name: "goods.add"
    });
    
    this.good = new Good();

    this._specialtiesService.get()
      .subscribe((response) => this.tags = response);

    this.form = new FormGroup({
      title: new FormControl('', [<any>Validators.required, <any>Validators.minLength(5)]),
      description: new FormControl(),
			price: new FormControl(),
			tags: new FormControl(),
			active: new FormControl('')
    });
  }

  clearForm(options?: any[]) : void {
    if(this.good != undefined) {
      this.good.clear();
    } else {
      this.good = new Good();
    }
  }

  onSubmit(values, valid) {
    if(valid) {
      this._goodsService.add(values)
        .subscribe((data) => this._toastService.toast("Товар успешно добавлен!"));
    }
  }

}



