import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { FileSelectService } from '../../file-select.service';

@Component({
  selector: 'app-selected-files',
  templateUrl: './selected-files.component.html',
  styleUrls: ['./selected-files.component.css']
})
export class SelectedFilesComponent implements OnInit {
  @Input('buttonTitle') buttonTitle: string = "Выбрать изображения";
  @Input('multiple') multiple: boolean = false;
  @Input('files') files: any[] = [];

  @Output('select') eSelect: EventEmitter<any[]> = new EventEmitter<any[]>();
  @Output('beforeSelect') eBeforeSelect: EventEmitter<any> = new EventEmitter<any>();

  constructor(
    private _fss: FileSelectService,
    private _router: Router
  ) { }

  ngOnInit() {
    let startLength = this.files.length;
    
    if(!this._fss.isEmpty()) {
      this.files = this._fss.get();
      this.eSelect.emit(this.files);
    }
  }

  selectImage() {
    this.eBeforeSelect.emit();

    this._fss.clear();
    if(!this.multiple) this._fss.single();
    this._fss.start();
    
    if(this.files.length > 0) {
      this.files.forEach((file) => this._fss.add(file));
    }

    this._router.navigate(['/uploads']);
  }

}



