import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-autocomplete',
  templateUrl: './autocomplete.component.html',
  styleUrls: ['./autocomplete.component.css']
})
export class AutocompleteComponent implements OnInit {
  @Input('items') items = [];
  @Input('time') time: number = 1000;

  @Output('searchChange') eKeyPress: EventEmitter<any> = new EventEmitter<any>();
  @Output('selectItem') eSelect: EventEmitter<any> = new EventEmitter<any>();
  @Output('close') eClose: EventEmitter<any> = new EventEmitter<any>();
  
  showItems: boolean = false;
  search: string;
  private timer;

  constructor() { }

  onKeyPress(target) {
    if(this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }
    
    this.timer = setTimeout(() => this.eKeyPress.emit(this.search), this.time);
  }

  onKeyDown(target) {
     if(target.key === 'Escape') {
        this.onHideItems();
     } else if(target.key == 'Backspace') {
        this.onKeyPress(target);
     }
  }

  onClick(item) {
    this.eSelect.emit(item);
    this.onHideItems();
  }

  onClickOutside() {
    this.onHideItems();
  }

  onClean() {
    this.eClose.emit();
  }

  onShowItems() {
    this.showItems = true; 
  }

  onHideItems() {
    this.showItems = false;
  }

  ngOnInit() {
  }

}



